:root {
  font-size: 16px;
  font-family: "Open Sans";
  --text-primary: #b6b6b6;
  --text-secondary: #ececec;
  --bg-primary: #23232e;
  --bg-secondary: #141418;
  --transition-speed: 1000ms;
}

#root {
  margin: 0;
  padding: 0;
  background-color: orange;
}

body {
  color: black;
  background-color: #fff;
  margin: 0;
  padding: 0;
}

body::-webkit-scrollbar {
  width: 0.5rem;
}

body::-webkit-scrollbar-track {
  background: #83838a;
}

body::-webkit-scrollbar-thumb {
  background: #2a0b80;
}

main {
  margin-left: 5rem;
}

.navbar {
  position: absolute;
  width: 5rem;
  height: 100vh;
  position: fixed;
  background-color: var(--bg-primary);
  transition: width 200ms ease;
}

.navbar-nav {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  /* text-decoration: none; */
}

.nav-item {
  width: 100%;
}

.nav-item:last-child {
  margin-top: auto;
}

.nav-link {
  display: flex;
  align-items: center;
  height: 5rem;
  color: var(--text-primary);
  text-decoration: none;
  filter: grayscale(100%) opacity(0.7);
  transition: var(--transition-speed);
}

.nav-link:hover {
  filter: grayscale(0%) opacity(1);
  background: var(--bg-secondary);
  color: var(--text-secondary);
}

.link-text {
  display: none;
}

.nav-link .icon {
  min-width: 2rem;
  margin: 0 1.5rem;
}

.navbar:hover {
  width: 12rem;
}

.navbar:hover .link-text {
  display: block;
  text-decoration: none;
}

.fa-primary {
  color: #b0b8ff;
}

.fa-secondary {
  color: #df49a6;
}

.fa-primary,
.fa-secondary {
  transition: var(--transition-speed);
}

.logo {
  background-color: green;
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 1rem;
  text-align: center;
  color: var(--text-secondary);
  background: var(--bg-secondary);
  font-size: 1.5rem;
  letter-spacing: 0.3ch;
  width: 100%;
}

.nav-item .arrows {
  transform: rotate(0deg);
  transition: transform var(--transition-speed);
}

.navbar:hover .nav-item .arrows {
  transform: rotate(-360deg);
}

.about {
  color: white;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  box-sizing: border-box;
}

