}
.name {
  font-size: 80px;
  line-height: 1.1;
  margin: 0px;
  font-weight: 600;
  color: rgb(204, 214, 246);
}

.hammer {
  font-size: 25px;
  color: rgb(204, 214, 246);
  vertical-align: top;
  box-sizing: border-box;
  position: absolute;
}
.hammer:hover {
  font-size: 27px;
  color: rgb(184, 198, 241);
  vertical-align: top;
  box-sizing: border-box;
}

.name:hover {
  font-size: 80px;
  line-height: 1.1;
  margin: 0px;
  font-weight: 600;
  color: rgb(223, 229, 250);
}
.build {
  font-size: 50px;
  line-height: 1.1;
  margin: 0px;
  font-weight: 300;
  color: rgb(204, 214, 246);
}

a {
  text-decoration: none;
}
